import React, { useState } from "react";
import { Link } from "react-router-dom";
import items from "../../assets/data/item";



const About = ()=> {

    const [data] = useState({
        subtitle: "About Us",
        title: "Height Quality NFT Collections",
        desc1: "Sed ut perspicitis unde comnis iste natuus enim ad minim veniam, quis nostrud",
        desc2: "Duis aute irure dolor in reprehenderit in volutate velit esse cillum dolore"
    })


    return <section className="tf-section section-about">
        <div className="container">
            <div className="row reverse">
                <div className="col-xl-7 col-md-12">
                    <div className="group-image">
                        <div className="left">
                            <div className="item bg-1"><img src={items.item8} alt="" /></div>
                        </div>
                        <div className="right">
                            <div className="item bg-2"><img src={items.item9} alt="" /></div>
                            <div className="item bg-3"><img src={items.item10} alt="" /></div>
                        </div>
                    </div>
                </div>
                <div className="col-xl-5 col-md-12">
                    <div className="block-text pt-12">
                        <h5 className="sub-title mb-10" data-aos-duration="1000">{data.subtitle}</h5>
                        <h3 className="title mb-58" data-aos-duration="1000">{data.title}</h3>
                        <p className="fs-21 mb-33" data-aos-duration="1000" data-aos="fade-up">{data.desc1}</p>
                        <p className="fs-18 line-h17 mb-41" data-aos-duration="1000" data-aos="fade-up">{data.desc2}</p>
                        <Link to= "/" className="btn-action style-2" data-aos-duration="1200" data-aos="fade-up">More about Us</Link>
                    </div>
                </div>
            </div>
        </div>

    </section>
}

export default About;

