import React,{useState, useEffect} from "react";
import  "./header.css"
import { Link } from "react-router-dom";
import logo from "../../assets/data/logo";
import menus from "../../assets/data/menus";
import Button from "../button/ButtonOne";

const Header = () => {
    const [scroll, setScroll] = useState(false)
    
    useEffect(()=>{
        window.addEventListener("scroll", ()=>{
            setScroll(window.scrollY > 100)
        })
        return ()=> {
            setScroll({})
        }
    },[])
    const [menuActive, setMenuActive] = useState(false)
    const handleMenuActive = ()=> {
        setMenuActive(!menuActive)
    }

    return <header id="header_main" className={`header js-header ${scroll ? 'is-fixed' : ''}`}>
        <div className="container">
            <div className="row">
                <div className="col-12">
                    <div className="header_body d-flex justify-content-between">
                        <div className="header_logo">
                            <Link to= "/">
                                <img src={logo} alt="" id="site-logo"/>
                            </ Link>
                        </div>
                        <div className="header_right">
                            <nav id="main-nav" className={`main-nav ${menuActive ? 'active' : ''}`}>
                                <ul id="menu-primary-menu" className="menu">
                                {
                                    menus.map((data, index)=> (
                                        <li key={index} className={`menu-item`}>
                                            <a href={data.links}>{data.name}</a>
                                        </li>
                                    ))
                                }
                                </ul>
                            </nav>
                            <div className="button">
                                <Button />
                            </div>
                            <div className={`mobile-button ${menuActive ? 'active' : ''}`} onClick={handleMenuActive}>
                                <span></span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </header>
}

export default Header;